import { useTranslation } from "react-i18next";
import style from "./ExcelImport.module.css";
import { memo, useRef, useState } from "react";
import { assestsUserFinanceUrl } from "../Services/UrlConfig/AssestsBaseUrl";
import UploadExcelButton from "../Ui/Button/UploadExcelButton";
import CreateButton from "../Ui/Button/CreateButton";

const ExcelGrabFile = ({ setFile, setStep }) => {
  const { t } = useTranslation();
  const [dragActive, setDragActive] = useState(false);
  const inputRef = useRef(null);

  const onDrag = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (e.type === "dragenter" || e.type === "dragover") {
      setDragActive(true);
    } else if (e.type === "dragleave") {
      setDragActive(false);
    }
  };

  const onPaste = (e) => {
    if (e.clipboardData.files.length) {
      setFile(e.clipboardData.files[0]);
      setStep(1);
    }
  };

  const onDropFile = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setDragActive(false);
    if (e.dataTransfer.files && e.dataTransfer.files[0]) {
      setFile(e.dataTransfer.files[0]);
      setStep(1);
    }
  };

  const onChange = function (e) {
    e.preventDefault();
    if (e.target.files && e.target.files[0]) {
      setFile(e.target.files[0]);
      setStep(1);
    }
  };

  const onButtonClick = () => {
    inputRef.current.click();
  };

  return (
    <>
      <h2 className={style.mcuy_file}>{t("upload_your_file")}</h2>

      <div className={style.mcf_card}>
        <form
          onDragEnter={onDrag}
          onSubmit={(e) => e.preventDefault()}
          onPaste={onPaste}
          className={style.mcf_container}
        >
          <UploadExcelButton
            onExcelFileUpload={onChange}
            assestUrl={`${assestsUserFinanceUrl}/xsxl.svg`}
          />
          <CreateButton
            label={t("upload_file")}
            onAction={onButtonClick}
            customStyle={{
              margin: "0",
              marginTop: "0.5rem",
              padding: "0.2rem 0.8rem",
              fontWeight: "400",
              fontSize: "13px",
            }}
          />
          <input
            ref={inputRef}
            type="file"
            id="excel_grab_file"
            multiple={false}
            onChange={onChange}
            style={{
              display: "none",
            }}
          />
          <label
            id="label-file-upload"
            htmlFor="excel_grab_file"
            className={dragActive ? "drag-active" : ""}
            style={{ height: "auto" }}
          >
            <p
              className={style.mcf_card_paragraph}
              style={{
                marginTop: "1rem",
              }}
            >
              or drag and drop it here
            </p>
          </label>
          <div className={style.mcf_card_paragraph}>or paste a file</div>

          {dragActive && (
            <div
              onDragEnter={onDrag}
              onDragLeave={onDrag}
              onDragOver={onDrag}
              onDrop={onDropFile}
              className={style.mcf_drag_area}
            />
          )}

          <div className={style.mcfc_sample}>{t("download_a_sample")}</div>
        </form>
      </div>
    </>
  );
};

export default memo(ExcelGrabFile);
