import { useTranslation } from "react-i18next";
import style from "./ExcelImport.module.css";
import FormInput from "../Ui/Input/FormInput";
import { useEffect, useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../Validation/functions/checkingRequiredField";
import AddButtonWrapper from "../Ui/Button/AddButtonWrapper";
import CreateButton from "../Ui/Button/CreateButton";

const ExcelExtraInfoImport = ({
  keyCount,
  rootList,
  onImportFileInSystem,
  file,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    excel_sheet_name: "",
  });

  useEffect(() => {
    if (file?.name) {
      setState((prev) => ({
        ...prev,
        excel_sheet_name: file?.name,
      }));
    }
  }, [file?.name]);
  const [errorField, setErrorField] = useState("");
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onImportFile = () => {
    let validation = requiredField(state, []);

    if (existingRequiredField(validation)) {
      onImportFileInSystem(rootList, keyCount, state);
    } else {
      setErrorField(validation);
    }
  };
  // console.info("file", file);
  return (
    <>
      <h2
        className={style.mcuy_file}
        style={{
          marginBottom: "1rem",
        }}
      >
        {t("your_file_is_ready_to_be_imported")}
      </h2>
      <h6
        className={style.mcuyf_note}
        style={{
          marginBottom: "4rem",
        }}
      >
        {t(
          "take_a_quick_look_at_these_settings_to_make_sure_nothing_is_missed_out"
        )}
      </h6>
      <div className={style.mcfd_card}>
        <FormInput
          label={t("file_name")}
          placeholder={t("file_name_placeholder")}
          name={"excel_sheet_name"}
          value={state.excel_sheet_name}
          type={"text"}
          onChange={onInputChange}
          errorShow={errorField["excel_sheet_name"]}
        />
        <AddButtonWrapper
          customStyle={{
            justifyContent: "center",
          }}
        >
          <CreateButton
            label={t("import")}
            onAction={onImportFile}
            customStyle={{
              width: "fit-content",
              paddingInline: "2rem",
            }}
          />
        </AddButtonWrapper>
      </div>
    </>
  );
};

export default ExcelExtraInfoImport;
