import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../UiInput.module.css";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
// import BackdropModal from "../../Utils/Modal/BackdropModal";
const CustomSelectSearch = ({
  selectLabel,
  onClick,
  selectedValue,
  options,
  customStyleContainer,
  customLabelStyle,
  customListStyle,
  errorShow,
  defalutValue,
  isDenied,
  customStyleArea,
}) => {
  const { t } = useTranslation();
  const [openOption, setOpenOption] = useState(false);
  const [value, setValue] = useState("");
  const [filter, setFilter] = useState(options ?? []);

  useEffect(() => {
    if (options?.length) {
      setFilter(options);
    }
  }, [options?.length]);
  const onSelectFunction = () => {
    if (isDenied) {
    } else setOpenOption((pre) => !pre);
  };

  useEffect(() => {
    if (defalutValue) {
      setValue(defalutValue);
    }
  }, [defalutValue]);

  const onChooseOption = (val) => {
    onClick(val);
    onSelectFunction();
    if (val?.name) setValue(val?.name);
    else setValue(val);
  };

  const onSearchEvent = (e) => {
    if (e.target.value) {
      let data = getFilterData(options, e.target.value);
      setFilter(data);
    } else {
      setFilter(options);
    }
  };
  return (
    <div className={style.form_input_container} style={customStyleContainer}>
      <p className={style.join_form_select_paragraph} style={customLabelStyle}>
        {selectLabel}
        {errorShow && (
          <span style={{ color: "red", marginLeft: "15px" }}>
            * {t("form_require_label")}
          </span>
        )}
      </p>
      <section
        className={style.join_form_select_custom}
        onClick={onSelectFunction}
        style={customStyleArea}
      >
        {!value ? (
          <p className={style.join_form_select_custom_paragraph}>
            {selectedValue}
          </p>
        ) : (
          <p className={style.join_form_select_custom_paragraph}>
            {value ?? ""}
          </p>
        )}
        {/* </>
        )} */}

        {openOption ? (
          <img
            className={style.join_form_select_custom_rotate_icon}
            src={`${assestsUserFinanceUrl}/down.svg`}
            alt="categor icon"
          />
        ) : (
          <img
            className={style.join_form_select_custom_icon}
            src={`${assestsUserFinanceUrl}/down.svg`}
            alt="categor icon"
          />
        )}
      </section>
      {openOption && (
        <>
          {/* <BackdropModal
            onBackdropClose={onSelectFunction}
            customBackdropStyle={{
              backgroundColor: "transparent",
              zIndex: "1100",
            }}
          /> */}
          <section className={style.jfscl_option_list} style={customListStyle}>
            <div className={style.jfsclol_search_container}>
              <input
                type="text"
                placeholder={t("search")}
                className={style.jfsclolsc_input}
                onChange={onSearchEvent}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>

            {filter?.map((opt, index) => (
              <p
                key={index}
                onClick={() => onChooseOption(opt)}
                className={style.join_form_select_custom_paragraph_option}
              >
                {opt?.name ?? opt}

                {opt?.isRequired ? (
                  <span
                    style={{
                      color: "red",
                    }}
                  >
                    {" "}
                    *
                  </span>
                ) : null}
              </p>
            ))}
            {filter?.length === 0 ? (
              <p className={style.join_form_select_custom_paragraph_option}>
                {t("no_results_found")}
              </p>
            ) : (
              ""
            )}
          </section>
        </>
      )}
    </div>
  );
};

export default CustomSelectSearch;

const getFilterData = (list, search) => {
  let ft = list?.filter((dt) => {
    if (dt?.name?.toLowerCase()?.includes(search?.toLowerCase())) {
      return dt;
    } else {
      return null;
    }
  });
  return ft;
};
