import { memo, useEffect, useState } from "react";
import style from "./ExcelImport.module.css";
import { utils, read } from "xlsx";
import { useTranslation } from "react-i18next";
import CustomSelectSearch from "../Ui/Select/CustomSelectSearch";
import CreateButton from "../Ui/Button/CreateButton";
import {
  assestsUserAdmissionUrl,
  assestsUtilityUrl,
} from "../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContentWrapper from "../Ui/UserMember/UserMemberContentWrapper";
import AddButtonWrapper from "../Ui/Button/AddButtonWrapper";

const ExcelImportFile = ({
  grabFile,
  setStep,
  setKeyCount,
  onImportFileInSystem,
  keyCount,
  requiredFormat,
  whichImportAccess,
  setRootList,
}) => {
  const { t } = useTranslation();
  const [file, setFile] = useState(grabFile ?? "");
  const [mList, setMList] = useState([]);
  const [cList, setCList] = useState({});
  const [deSelectColumn, setDeSelectColumn] = useState({});
  const [format, setFormat] = useState([]);
  const [mappedList, setMappedList] = useState(grabFile ?? "");

  useEffect(() => {
    if (requiredFormat?.length) {
      setFormat(requiredFormat);
    }
  }, [requiredFormat?.length]);
  useEffect(() => {
    if (grabFile) {
      onExcelFileUpload(grabFile);
    }
  }, [grabFile]);

  const onExcelFileUpload = async (dataFile) => {
    const grab_file = dataFile;
    const data = await grab_file.arrayBuffer();
    const w_query = read(data);
    const w_sheet = w_query.Sheets[w_query.SheetNames?.[0]];
    // const data_query = utils.sheet_to_json(w_sheet, { raw: false });
    // const mod_f = [];
    // const m_f = [];
    // for (let i = 0; i < data_query?.length; i++) {
    //   let j = 0;
    //   let obj = {};
    //   for (let ob in data_query[i]) {
    //     let r_j = data_query[i][ob];
    //     if (i === 0) {
    //       m_f.push({
    //         title: ob,
    //         firstValue: r_j,
    //         isMapped: false,
    //         mappedKey: "",
    //         isRequired: false,
    //       });
    //     }
    //     obj[`key${j}`] = {
    //       value: r_j,
    //       fomat_key: "",
    //       excel_key: ob,
    //     };
    //     ++j;
    //   }
    //   mod_f.push(obj);
    // }
    // setFile(mod_f);
    // setMList(m_f);
    const data_query = utils.sheet_to_json(w_sheet, { raw: false });
    const m_f = [];
    for (let i = 0; i < data_query?.length; i++) {
      for (let ob in data_query[i]) {
        let r_j = data_query[i][ob];
        if (i === 0) {
          m_f.push({
            title: ob,
            firstValue: r_j,
            isMapped: false,
            mappedKey: "",
            isRequired: false,
            db_key: "",
          });
        }
      }
      break;
    }
    setFile(data_query);
    setMList(m_f);
  };

  const onChooseField = (key, data) => {
    let m = [...mList];
    let c_t = {};
    for (let i = 0; i < m?.length; i++) {
      if (m[i]?.title === key) {
        m[i].mappedKey = data?.name;
        m[i].isMapped = true;
        m[i].isRequired = data?.isRequired;
        m[i].db_key = data[data?.name];
        c_t[key] = {
          name: data?.name,
          [data?.name]: data[data?.name],
          isRequired: data?.isRequired,
        };
        // break;
      } else {
        if (m[i].mappedKey)
          c_t[m[i]?.title] = {
            name: m[i].mappedKey,
            [m[i].mappedKey]: m[i].db_key,
            isRequired: m[i].isRequired,
          };
      }
    }
    setMList(m);
    setCList(c_t);
    setDeSelectColumn((prev) => ({
      ...prev,
      [key]: true,
    }));

    // const c_t = {
    //   ...cList,
    //   [key]: {
    //     name: data?.name,
    //     [data?.name]: data[data?.name],
    //     isRequired: data?.isRequired,
    //   },
    // };
    // console.info(c_t);

    const f_t = [];
    for (let t of requiredFormat) {
      let flag = false;
      for (let c in c_t) {
        if (c_t[c]?.name === t?.name) {
          flag = true;
          break;
        }
      }
      if (!flag) f_t.push(t);
    }
    setFormat(() => f_t);
  };
  const onDeselectColumn = (key) => {
    if (cList[key] && !cList[key]["isRequired"])
      setDeSelectColumn((prev) => ({
        ...prev,
        [key]: prev[key] ? false : true,
      }));
  };
  const onFomattingDone = () => {
    const data_query = [...file];
    const mod_f = [];
    let count = 0;
    for (let i = 0; i < data_query?.length; i++) {
      let j = 0;
      let ct = 0;
      let obj = {};
      for (let ob in data_query[i]) {
        let r_j = data_query[i][ob];
        if (cList[ob]?.name) {
          obj[`key${j}`] = {
            value: r_j,
            fomat_key: cList[ob]?.name,
            excel_key: ob,
            db_key: cList[ob][cList[ob]?.name],
          };
          ++ct;
          ++j;
        }
        // ++j;
      }
      if (i === 0) {
        setKeyCount(ct);
        count = ct;
      }
      mod_f.push(obj);
    }
    setMappedList(mod_f);

    // console.info("mod_f", mod_f);
    if (whichImportAccess === "SCHOLARSHIP") {
      setRootList(mod_f);
      setStep(3);
    } else {
      onImportFileInSystem(mod_f, count);
    }
    // const inp = [...file];
    // for (let i = 0; i < inp?.length; i++) {
    //   let j = 0;
    //   for (let ob in inp[i]) {
    //     inp[i][`key${j}`]["fomat_key"] = cList[inp[i][ob]["excel_key"]];
    //     ++j;
    //   }
    // }
    // setFile(inp);
  };

  // console.info("deSelectColumn", mappedList);
  return (
    <>
      <UserMemberContentWrapper
        customStyle={{
          width: "100%",
        }}
      >
        <h2 className={style.mcuy_file}>
          {t("review_the_mapping_of_your_fields")}
        </h2>
        <div className={style.mc_excel_container}>
          <div className={style.mc_heading}>
            <h6
              style={{
                // "--mc-width": "100%",
                flex: "0.4 0.4 8.2%",
              }}
            >
              {t("import")}
            </h6>
            <h6
              style={{
                // "--mc-width": "50%",
                flex: "2 2 52%",
                textAlign: "left",
              }}
            >
              {t("xslx_column")}
            </h6>
            <h6
              style={{
                // "--mc-width": "15%",
                flex: "1 1 5%",
              }}
            >
              {t("mapped")}
            </h6>
            <h6
              style={{
                // "--mc-width": "25%",
                flex: "1 1 24%",
                marginRight: "1rem",
              }}
            >
              {t("suite_field")}
            </h6>
          </div>
          {mList?.map((lt) => (
            <section key={`key${lt?.title}`} className={style.mc_content}>
              <h6
                style={{
                  // "--mc-width": "10%",
                  flex: "0.4 0.4 8%",
                  opacity: deSelectColumn[lt?.title] ? "1" : "0.5",
                }}
              >
                <img
                  src={
                    deSelectColumn[lt?.title]
                      ? `${assestsUserAdmissionUrl}/check.svg`
                      : `${assestsUserAdmissionUrl}/uncheck.svg`
                  }
                  alt="icon"
                  style={{
                    height: "1.3rem",
                    borderRadius: "0",
                    cursor: "pointer",
                  }}
                  onClick={() => onDeselectColumn(lt?.title)}
                />
              </h6>
              <div
                style={{
                  // "--mc-width": "50%",
                  flex: "2 2 50%",
                  opacity: deSelectColumn[lt?.title] ? "1" : "0.5",
                }}
                className={style.mcc_inner}
              >
                <h6>{lt?.title}</h6>
                <p>
                  {lt?.firstValue?.substring(0, 70)}
                  {lt?.firstValue?.length > 70 ? "..." : ""}
                </p>
              </div>
              <h6
                style={{
                  // "--mc-width": "15%",
                  flex: "1 1 5%",
                  opacity: deSelectColumn[lt?.title] ? "1" : "0.5",
                }}
              >
                <img
                  src={
                    lt?.mappedKey
                      ? `${assestsUtilityUrl}/complete.svg`
                      : `${assestsUtilityUrl}/radio-grey.svg`
                  }
                  alt="icon"
                  style={{
                    height: "1.3rem",
                    borderRadius: "0",
                  }}
                />
              </h6>
              <CustomSelectSearch
                selectedValue={t("select_a_field")}
                customStyleContainer={{
                  // width: "25%",
                  flex: "1 1 25%",
                  paddingInline: "1rem",
                }}
                customStyleArea={
                  lt?.isRequired
                    ? {
                        border: "0.5px solid red",
                      }
                    : {}
                }
                options={format}
                onClick={(dt) => onChooseField(lt?.title, dt)}
                defalutValue={lt?.mappedKey}
                customListStyle={{
                  opacity: "1",
                }}
              />
            </section>
          ))}
        </div>
        <AddButtonWrapper
          customStyle={{
            justifyContent: "center",
          }}
        >
          <CreateButton
            label={t("next")}
            onAction={onFomattingDone}
            customStyle={{
              width: "fit-content",
              paddingInline: "2rem",
            }}
          />
        </AddButtonWrapper>
      </UserMemberContentWrapper>
    </>
  );
};

export default memo(ExcelImportFile);
