import { useState } from "react";
import ExcelImportWrapper from "./ExcelImportWrapper";
import ExcelGrabFile from "./ExcelGrabFile";
import ExcelImportFile from "./ExcelImportFile";
import ExcelExtraInfoImport from "./ExcelExtraInfoImport";
// import ExcelDateImport from "./ExcelDateImport";

const ExcelImport = ({
  onClose,
  onImportFileInSystem,
  requiredFormat,
  whichImportAccess,
}) => {
  const [step, setStep] = useState(0);
  const [file, setFile] = useState("");
  const [keyCount, setKeyCount] = useState(0);
  const [rootList, setRootList] = useState([]);

  // console.info("keyCount", keyCount);

  // const onImportFileInSystem=()=>{

  // }
  return (
    <ExcelImportWrapper onClose={onClose} step={step}>
      {
        step === 0 ? (
          <ExcelGrabFile setFile={setFile} file={file} setStep={setStep} />
        ) : step === 1 ? (
          <ExcelImportFile
            grabFile={file}
            setStep={setStep}
            setKeyCount={setKeyCount}
            onImportFileInSystem={onImportFileInSystem}
            keyCount={keyCount}
            requiredFormat={requiredFormat}
            whichImportAccess={whichImportAccess}
            setRootList={setRootList}
          />
        ) : step === 3 ? (
          <ExcelExtraInfoImport
            keyCount={keyCount}
            rootList={rootList}
            onImportFileInSystem={onImportFileInSystem}
            file={file}
          />
        ) : null
        // <ExcelDateImport grabFile={file} setStep={setStep} />
      }
    </ExcelImportWrapper>
  );
};

export default ExcelImport;
